
.header {
    margin: 0;
    padding: 0;
    background-color: red;
    
    
}
.my-classes {
    background-color: gainsboro;
    
}

.btn {
    font-family: monospace;
    background-color: rgb(57, 164, 226);
    color: white;
    font-size: 20px;
    padding: 5px 20px;
    border-radius: 5px;
    margin: 10px 10px;
    cursor: pointer;
}

.fav-classes {
    
    /* background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%); */

}

.add-fav {
    border-right: 2px solid hsl(0, 0%, 0%);
    animation: typewriter 2.5s steps(45) normal, blinkTextCursor .8s infinite;
    white-space: nowrap;
    overflow: hidden;
    text-align: center;
}
@keyframes typewriter {
    from {
        width: 0;
    }

    to {
        width: 30%;
    }
}

@keyframes blinkTextCursor {
    from {
        border-right-color: hsl(0, 0%, 0%);
    }

    to {
        border-right-color: transparent;
    }
}